import React, {useEffect} from 'react';
import { Switch, Route, useLocation } from 'react-router-dom'

import Layout from './containers/Layout'

import HomePage from './containers/HomePage'
import TrashPage from './containers/TrashPage'
import AllPage from './containers/TrashPage/AllPage'
import CataloguePage from './containers/CataloguePage'
import GamePage from './containers/GamePage'
import AllCards from './containers/GamePage/AllCards'
import PlayGame from './containers/GamePage/PlayGame'
import AboutPage from './containers/AboutPage'
import HowPage from './containers/HowPage'
import SupportPage from './containers/SupportPage'
import FaceEditor from './containers/FaceEditor';

function ScrollToTop() {
  const { pathname } = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [pathname]);

  return null;
}

function App() {
  return (
    <Layout>
      <ScrollToTop />
      <Switch>
        <Route path="/" exact component={HomePage} />
        <Route path="/catalogue" component={CataloguePage} />
        <Route path="/allTrashes/exploded" component={() => <AllPage exploded />} />
        <Route path="/allTrashes/belongsTo" component={() => <AllPage belongsTo />} />
        <Route path="/allTrashes/share" component={() => <AllPage share />} />
        <Route path="/allTrashes" component={AllPage} />
        <Route path="/trash/:id/face" component={FaceEditor} />
        <Route path="/trash/:id" component={TrashPage} />
        <Route path="/game/allCards" component={AllCards} />
        <Route path="/game/play" component={PlayGame} />
        <Route path="/game" component={GamePage} />
        <Route path="/about" component={AboutPage} />
        <Route path="/how" component={HowPage} />
        <Route path="/support" component={SupportPage} />
      </Switch>
    </Layout>

  );
}

export default App;
