import React from 'react'
import {
  Flex,
  Stack,
  useDisclosure,
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalBody,
  ModalCloseButton,
} from '@chakra-ui/react';

import Box from '../components/Box';
import Container from '../components/Container';
import Link from '../components/Link';
import Sponsor from '../components/Sponsor';
import { responsive } from '../components/ThemeProvider/theme';

const Footer = ({ isAbout, isTrash, noSep, ...props }) => {
  const { isOpen, onOpen, onClose } = useDisclosure()
  return (
    <Box bg="white" {...props}>
      {!isAbout && (
        <Container>
          <Sponsor
            isFooter
            textColor="black"
            borderTop={!noSep && '1px solid'}
            px="0"
            fontSize={responsive("2.5em", '1.375em')}
            logoProps={{
              width: responsive('15em', '10em'),
              mx: '1em',
            }}
          />
        </Container>
      )}
      <Container py="1em" fontSize={responsive('2em', '0.625em')}>
        <Flex justifyContent="center" fontSize={responsive('1em', '1.5em')} flexDirection={responsive('column-reverse', 'row')}>
          <Box mr={responsive('0', '4em')} textAlign={responsive('center', 'left')} py={responsive('1em', '0')}>
            Copyright © {new Date().getFullYear()} RE-THINK
          </Box>
          <Stack isInline spacing={responsive('2em', '1.5em')} px={responsive('2em', '0')} justifyContent={responsive('center', 'flex-start')}>
            <Link href="https://www.facebook.com/rethink.tw">RE-THINK FB</Link>
            <Box height="100%" borderLeft="2px solid" />
            <Link href="http://rethinktw.org/">RE-THINK 官網</Link>
            <Box height="100%" borderLeft="2px solid" />
            <Link onClick={onOpen} as="button">製作說明</Link>
          </Stack>
        </Flex>
      </Container>
      <Modal isOpen={isOpen} onClose={onClose} isCentered>
        <ModalOverlay bg="rgba(255,255,255,0.5)" />
        <ModalContent
          border="3px solid"
          fontSize={responsive('2.5em', '1em')}
          rounded={responsive('1em', '1.5em')}
          maxWidth={responsive('90vw', '50em')}
          p={responsive('1em', 0)}
        >
          <ModalHeader
            pt="2em"
            fontWeight="900"
            fontSize="1.25em"
          >這是，最通用的回收指南。<br />也是，需要你一起共創完成的指標。</ModalHeader>
          <ModalCloseButton
            bg="black"
            color="white"
            width="2em"
            height="2em"
            top={responsive('1.25em', '0.75em')}
            right={responsive('1.25em', '0.75em')}
            rounded="full"
            fontSize="1em"
            _hover={{
              bg: 'black',
              color: 'white',
            }}
            _active={{
              bg: 'black',
              color: 'white',
            }}
          />
          <ModalBody pb="2em" textAlign="justify">
          因為台灣的回收規範，會與時俱進，也因為地區不同、後端處理設施不同，而尚未有全國統一的標準答案。所以你在《回收大百科》所看到的資訊，已經是彙整了政府公告資訊、回收顧問專業、實際處理狀況等最大的交集。雖然你還是在少數狀況，會碰到規範有差異之處（例如在社區、辦公大樓等），或者清潔隊員不收等狀況，歡迎你回報給 <Link.MdLink href="mailto:service@rethinktw.org">RE-THINK 團隊</Link.MdLink>，讓我們可以持續更新內容、與政府及產業溝通和改變。<br />
          邀請你參與，一起為台灣創造最完整的《回收大百科》！
          </ModalBody>
        </ModalContent>
      </Modal>
    </Box>
  )
}

export default Footer
