import React from 'react'

import FullpageVerticalCenter from '../../components/FullpageVerticalCenter';
import Container from '../../components/Container';
import Box from '../../components/Box';
import Flex from '../../components/Flex';
import Heading from '../../components/Heading';
import Image from '../../components/Image';
import Button from '../../components/Button';

import useShowHeader from '../../contexts/header/useShowHeader';

import bannerSvg from './banner.svg'
import theme, { responsive } from '../../components/ThemeProvider/theme';

const GamePage = () => {
  useShowHeader('yellow.500')
  return (
    <FullpageVerticalCenter bg="yellow.500" mt="0">
      <Box mt={responsive('8em', 0)}>
        <Image src={bannerSvg} />
      </Box>
      <Container px="2em" mb={responsive('2em', '2em')}>
        <Flex mt={responsive('4em', '-4em')} alignItems="center" justifyContent="center" flexDirection={responsive('column', 'row')}>
          <Heading lineHeight="1.5" mr={responsive(0, '1em')} fontSize={responsive('3em', '1.75em')}>隨手丟垃圾很容易，但你真的懂丟嗎？<br />測看看30秒內，你可以丟對幾個垃圾！</Heading>
          <Button.Pink border="0.125em solid black" my="1em" to="/game/play" fontSize={responsive('4em', '1.75em')} height="2em" px="1em">開始遊戲</Button.Pink>
        </Flex>
      </Container>
    </FullpageVerticalCenter>
  )
}

export default GamePage
