import React from 'react'

import Flex from '../../../components/Flex'
import Box from '../../../components/Box'
import Button from '../../../components/Button'
import BackgroundImage from '../../../components/BackgroundImage'

import clock from './timer.svg'
import { ReactComponent as Trash } from './trash.svg'
import { ReactComponent as Recycle } from './recycle.svg'
import { responsive } from '../../../components/ThemeProvider/theme'

const CircleButton = props => (
  <Box
    width={responsive('20em', '8em')}
    height={responsive('20em', '8em')}
    transform={responsive('', 'translateY(-35vh)')}
  >
    <Button.Icon
      bg="white"
      rounded="full"
      width="100%"
      height="100%"
      _disabled={{
        opacity: 1,
      }}
      {...props}
    />
  </Box>
)

const Controls = ({ onAnswer, disabled }) => {
  return (
    <Flex position="absolute" left="0" right="0" bottom={responsive('15em', '1em')} justifyContent="space-around" alignItems="center">
      <CircleButton border="0.125em solid black" isDisabled={disabled} onClick={() => onAnswer(-1)} icon={<Recycle style={{ width: '100%' }} />} />
      <Box width={responsive('12em', '5em')}>
        {/* <BackgroundImage ratio={1} src={clock}>
          <Box.AbsCenter top="58%" color="pink.300" fontSize={responsive('6em', '2em')} fontWeight="900">{time}</Box.AbsCenter>
        </BackgroundImage> */}
      </Box>
      <CircleButton border="0.125em solid black" isDisabled={disabled} onClick={() => onAnswer(1)} icon={<Trash style={{ width: '100%' }} />} />
    </Flex>
  )
}

export default Controls
