import React from 'react'

import BackgroundImage from '../../../components/BackgroundImage'
import Text from '../../../components/Text'
import Box from '../../../components/Box'

import bg from './hashtag-shape.svg'
import { SizeMe } from 'react-sizeme'

const label = {
  A: '高回收價值',
  B: '中回收價值',
  C: '低回收價值',
}

const Hashtag = ({ children, color }) => {
  return (
    <BackgroundImage ratio={1332 / 615} src={bg}>
      <Box.Absolute left="8%" top="42%" transform="translateY(-50%) rotate(-12deg)" width="100%">
        <SizeMe>
          {({ size }) => (
            <Text fontSize={size.width ? `${Math.floor(size.width / 7)}px` : 0} fontWeight="900" color={color}>＃{label[children]}</Text>
          )}
        </SizeMe>

      </Box.Absolute>
    </BackgroundImage>
  )
}

export default Hashtag
